@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700");
@import url(https://fonts.googleapis.com/css?family=Oswald);

html,
body {
  margin: 1em;
  padding: 0;
  font-family: sans-serif;
  font-size: 18px;
  line-height: 1.5;
  background: rgb(201, 235, 245);
}
h1 {
  line-height: 1.25;
  margin: 0.5em 0 0;
}

.applymargin {
  margin-top: 5em;
  margin-left: 5em;
}
.bookmark {
  margin-top: 1.1em;
  margin-left: 7em;
  float: left;
  width: 3em;
  border-radius: 3em;
  border: 0.15em solid black;
  text-align: center;
}

#bookmarkspan {
  cursor: pointer;
}
